function timer() {
	// Таймер обратного отсчёта
	let deadline = "2021-01-01",
		getTimeRemaining = (endtime) => { // ES6
			let t = Date.parse(endtime) - Date.parse(new Date()),
			seconds = Math.floor((t / 1000) % 60), // получаем кол-во секунд (работает и без Math.floor)
			minutes = Math.floor((t / 1000 / 60) % 60),
			hours = Math.floor(t / (1000 * 60 * 60));
		
			return { // извлечение общего времени и ч, м, с
				"total": t,
				"hours": hours,
				"minutes": minutes,
				"seconds": seconds
			}
		},
		setClock = (id, endtime) => { // изменение файла HTML (ES6)
			let timer = document.getElementById(id), // установка и запуск часов
			hours = timer.querySelector(".hours"),
			minutes = timer.querySelector(".minutes"),
			seconds = timer.querySelector(".seconds"),
			updateClock = () => {
				let t = getTimeRemaining(endtime),
					addZero = (num) => { 
						if (num <= 9) {
							return '0' + num;
						} else return num;
					};
				
				hours.textContent = addZero(t.hours);
				minutes.textContent = addZero(t.minutes);
				seconds.textContent = addZero(t.seconds);
				
				if (t.total <= 0) {
					clearInterval(timeInterval);
					hours.textContent = '00';
					minutes.textContent = '00';
					seconds.textContent = '00';
				}
			},
			timeInterval = setInterval(updateClock, 1000);
		};
	
	setClock("timer", deadline);
};

module.exports = timer;